:root{
  --accent: #2b6cb0;
  --muted: #6b7280;
}
*{box-sizing:border-box}
body{font-family:Inter, ui-sans-serif, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial; margin:0; color:#111}
.app{display:flex; min-height:100vh; flex-direction:column}
header{background:var(--accent); color:white; padding:12px 0}
.container{flex:1; padding:24px; max-width:1100px; margin:0 auto; width:100%}
nav .brand{font-weight:700; font-size:20px; margin-right:16px}
.nav-links{display:flex; gap:12px; align-items:center}
.grid{display:grid; grid-template-columns:repeat(auto-fill,minmax(220px,1fr)); gap:16px}
.card{border:1px solid #e5e7eb; padding:12px; border-radius:8px; background:white}
.card img{width:100%; height:220px; object-fit:cover; border-radius:4px}
.badge{display:inline-block; padding:4px 8px; border-radius:999px; font-size:12px; background:#edf2ff; color:var(--accent); margin-bottom:8px}
.price{font-weight:700; margin-top:8px}
.btn{background:var(--accent); color:white; border:none; padding:8px 12px; border-radius:6px; cursor:pointer}
.btn.secondary{background:transparent; color:var(--accent); border:1px solid var(--accent)}
footer{background:#f8fafc; padding:16px; text-align:center; color:var(--muted); margin-top:24px}
.cart-list{display:flex; flex-direction:column; gap:10px}
.cart-item{display:flex; gap:12px; align-items:center; padding:10px; border:1px dashed #e6edf3; border-radius:6px}
form .field{margin-bottom:10px}
@media (max-width:600px){ .container{padding:12px} .card img{height:160px} }
